@mixin fieldset () {
  font-size: rem(14);
  border: none;
}
@mixin input () {
  border: 1px solid rgb(18, 97, 187);
  background-color: white;
  height: rem(30);
  width: rem(200);
  border-radius: 5px;
  text-align: center;

}

@mixin parseLink () {
  text-decoration: none;
  color:black;
  cursor: pointer;
}


